body, html {
  height: 100%;
  width: 100%; }

.angular-google-map-container {
  /*@extend  .col-md-12;  */
  height: 100%;
  width: 96%;
  left: 16px;
  position: absolute; 
  @media screen and (max-width: 453px ) {
    width: 90%;
    left: 18px;
  }

  @media screen and (min-width: 1110px ) {
    width: 97%;
    left: 12px;
  }

}

.location-search {

  padding-bottom: 20px;
  margin-top: -32px;
}

.middle {
  display: table;
  width: 80%;
  margin-left: auto;
  margin-right: auto; 
}
#map_canvas {
  height: 400px
}

